.form-control:focus {
  border-color: #ffc000;
  box-shadow: 0 0 0 0.25rem rgb(255, 192, 0, 0.25);
}

.form-select:focus {
  border-color: #ffc000;
  box-shadow: 0 0 0 0.25rem rgb(255, 192, 0, 0.25);
}

.btn-primary {
  border-color: #ffc000;
  background-color: #ffc000;
}

.btn-primary:hover {
  border-color: #ffc000;
  background-color: #ffc000;
}

.btn-primary:active {
  border-color: #ffc000;
  background-color: #ffc000;
}

.btn-primary:focus {
  border-color: #ffc000;
  background-color: #ffc000;
  box-shadow: 0 0 0 0.25rem rgb(255, 192, 0, 0.5);
}

.form-check-input:checked {
  background-color: #ffc000;
  border-color: #ffc000;
}
