.accordion__section {
  display: flex;
  flex-direction: column;
}

.accordion {
  background-color: #fff;
  color: #444;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.6s ease;
}

.accordion p,
svg {
  margin: 2rem 0;
}

.accordion__title {
  font-weight: 700;
  font-size: 1.3rem;
}

.accordion__icon {
  margin-left: auto;
  transition: transform 0.6s ease;
}

.accordion__content {
  background: #fff;
  overflow: hidden;
  transition: max-height 0.6s ease;
  border-bottom: 2px solid #000;
}

.accordion__text {
  font-weight: 400;
  padding: 18px;
}
