.mb-8 {
  margin-bottom: 6rem;
}

.banner-shape::before {
  width: 50%;
  height: 100%;
  background: #fbe4c4;
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
  border-bottom-left-radius: 30%;
}

.banner {
  position: relative;
}

h1 {
  font-weight: 700;
  line-height: 1.3;
}

section {
  padding: 5rem 0;
}
